<template>
  <div class="big">
    <div
      class="pic"
      :style="
        songcardheade?.coverImgUrl &&
        `background-image: URL(${songcardheade.coverImgUrl}?imageView=1&type=webp&thumbnail=370x0)`
      "
    >
      <div class="leftImg">
        <span>歌单</span>
        <p class="play">
          {{
            songcardheade.playCount / 10000 > 10000
              ? (songcardheade.playCount / 100000000).toFixed(1) + "亿"
              : (songcardheade.playCount / 10000).toFixed(1) + "万"
          }}
        </p>
        <img
          :src="
            songcardheade?.coverImgUrl &&
            `${songcardheade.coverImgUrl}?imageView=1&type=webp&thumbnail=253x0`
          "
          alt=""
        />
      </div>
      <div class="rightDes">
        <span class="biaoti">{{ songcardheade.name }}</span>
        <img
          class="active"
          :src="
            songcardheade?.creator &&
            songcardheade?.creator?.avatarUrl +
              '?imageView=1&type=webp&thumbnail=61x0'
          "
          alt=""
        />
        <span class="activename">{{ songcardheade?.creator?.nickname }}</span>
      </div>
    </div>
    <p class="head">歌曲列表</p>
    <ul class="sing">
      <li
        v-for="(s, i) in song"
        :key="i"
        @click="$emit('clickHandler', [s, song])"
      >
        <div class="left" :style="{ color: res.id == s.id ? 'red' : 'black' }">
          <span class="index">{{ i + 1 }}</span>
          <div class="singer">
            <div class="songnane">{{ s?.name }}</div>
            <p class="song">{{ s.ar[0].name }} - {{ s.al.name }}</p>
          </div>
        </div>
        <div class="hot-play" v-if="!(res.id == s.id)"></div>
        <div
          class="playing"
          v-if="res.id == s.id"
          :class="{ running: autoplay }"
        >
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
      </li>
    </ul>
    <!-- <p class="tell">精彩评论</p> -->
    <CommentsDetails
      :hotcomments="hotcomments"
      :newcomments="newcomments"
      :total="total"
    />
  </div>
</template>
<script>
import CommentsDetails from "@/components/CommentsDetails.vue";
export default {
  components: {
    CommentsDetails,
  },
  props: ["id", "clickHandler", "res", "autoplay"],
  data() {
    return {
      songcardheade: [],
      song: [],
      hotcomments: [],
      newcomments: [],
      total: null,
    };
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail", {
        params: { id: this.$route.query.id },
      })
      .then((res) => {
        // console.log(res.data.playlist);
        this.songcardheade = res.data.playlist;
        this.song = res.data.playlist.tracks;
      });
    this.axios
      .get("https://apis.netstart.cn/music/comment/playlist", {
        params: { id: this.$route.query.id },
      })
      .then((res) => {
        // console.log(res.data.comments);
        // console.log(res.data.hotComments);
        // console.log(res.data.total);
        // console.log(res.total);
        // res 后面永远都是data 别犯傻
        // console.log(res);
        this.hotcomments = res.data.hotComments;
        this.newcomments = res.data.comments;
        this.total = res.data.total;
      });
    // this.axios
    //   .get("https://apis.netstart.cn/music/comment/album", {
    //     params: { id: this.$route.query.id, limit: 2 },
    //   })
    //   .then((res) => {
    //     console.log(res);
    //   });
  },
};
</script>

